import { useState } from "react"

const LoansCalculator = () => {
    const [principal, setPrincipal] = useState(0)
    const [interest, setInterest] = useState(0)
    const [time, setTime] = useState(0)

    const [submit, setSubmit] = useState(false)
    const [total, setTotal] = useState(0)
    const [totalPayment, setTotalPayment] = useState(0)
    const [totalInterest, setTotalInterest] = useState(0)
    const [info, setInfo] = useState(false)

    function handlePrincipal(e) {
        setPrincipal(e.target.value)
    }

    function handleInterest(e) {
        setInterest(e.target.value)
    }

    function handleTime(e) {
        setTime(e.target.value)
    }

    function handleSubmit() {
        setSubmit(true)
        setTotal((Number(principal) * ((Number(interest)/1200)*(1+Number(interest)/1200)**(Number(time))) / ((1+Number(interest)/1200)**(Number(time))-1)).toFixed(2))
        setTotalPayment((Number(principal) * Number(time) * ((Number(interest)/1200)*(1+Number(interest)/1200)**(Number(time))) / ((1+Number(interest)/1200)**(Number(time))-1)).toFixed(2))
        setTotalInterest(((Number(principal) * Number(time) * ((Number(interest)/1200)*(1+Number(interest)/1200)**(Number(time))) / ((1+Number(interest)/1200)**(Number(time))-1))-Number(principal)).toFixed(2))
    }

    function toggleInfo() {
        setInfo(!info)
    }

  return (
    <div className="budget-breakdown">
    <div className="budget">
        <h3>Loans Calculator</h3>
        <p>Determine an estimate of how much you'll pay monthly to repay your student loans.</p>
        <div>
            <label>Loan Amount ($)</label>
            <input type='number' value={principal} onChange={handlePrincipal}/>
        </div>

        <div>
            <label>Interest Rate (%)</label>
            <input type='number' value={interest} onChange={handleInterest}/>
        </div>

        <div>
            <label>Time to repay (months)</label>
            <input type='number' value={time} onChange={handleTime}/>
        </div>

        <button onClick={handleSubmit}>Submit</button>
    </div>


    {submit && 
        <div className="budget-result">
            <h3>Monthly Repayment Amount: ${total}</h3>
            <p>Total Interest: ${totalInterest}</p>
            <p>Total Payment: ${totalPayment}</p>
            <div className="info-btn" onClick={toggleInfo} >?</div>
            {info && <div className="info" >
                    Loan Repayment is calculated by the formula:<br></br>
                    <h1 className="formula"><div className="a-p">A = P</div><div className="numerator">r(1+r)^n</div><div className="division"></div><div className="denominator">r(1+r)^n - 1</div></h1><br></br>
                    A = Principal loan amount<br></br>
                    r = Interest rate per period (as a decimal, not percentage)<br></br>
                    n = Number of periods
                </div>}
        </div>
    }
    </div>
  )
}

export default LoansCalculator