import Illustration from './images/links-illustration.svg'


const UsefulLinks = () => {
  return (
    <div className="body page-body">
        <div className="word-intro">
            <div>
                <h2>Useful Links</h2>
                <p>Here are resources you can use to learn more about financial literacy and paying for school</p>
            </div>
            <img className="test-illustration" src={Illustration} />
        </div>

            
        <div className='useful-links'>
            <h1>Paying for School</h1>
            <p><a href='https://www.canada.ca/en/services/benefits/education/student-aid/grants-loans.html' target='_blank'>Canada Grants and Loans</a></p>
            <p><a href='https://scholartree.ca/' target='_blank'>ScholarTree</a></p>
            <p><a href='https://studentawards.com/' target='_blank'>Student Awards</a></p>
            <p><a href='https://www.scholarshipscanada.com/' target='_blank'>Scholarships Canada</a></p>
            <p><a href='https://www.canada.ca/en/services/benefits/education/education-savings/resp.html' target='_blank'>Registered Education Savings Plan (RESP)</a></p>

            <h1>Learning Finance</h1>
            <p><a href='https://www.investopedia.com' target='_blank'>Investopedia</a></p>
            <p><a href='https://www.cibc.com/en/student/student-life/scoring-high-with-credit.html' target='_blank'>CIBC Crash Course on Credit</a></p>

            <h1>Tools</h1>
            <p><a href='https://www.cibc.com/en/personal-banking/smart-advice/tools-calculators/student-budget-calculator.html' target='_blank'>CIBC Student Budget Calculator</a></p>
            
    
        </div>
    </div>
  )
}

export default UsefulLinks